.all {
  overflow: scroll;
}
.all::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.trailer-view {
  width: 1020px;
  height: 389px;
  border-radius: 20px;
  background: lightgray 50% / cover no-repeat;
  display: block;
  margin-left: 30px;
}
.trailer-image {
  width: 1020px;
  height: 389px;
  border-radius: 20px;
}
.watch {
  width: 168px;
  height: 157px;
  flex-shrink: 0;
  position: relative;
  margin: 0;
  left: 450px;
  bottom: 300px;
}
.round {
  width: 110px;
  height: 110px;
  flex-shrink: 0;
  fill: rgba(255, 255, 255, 0.35);
  stroke-width: 2px;
  stroke: rgba(232, 232, 232, 0.2);
  filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.25));
  backdrop-filter: blur(2px);
}
.play {
  width: 54px;
  height: 54px;
  flex-shrink: 0;
  position: relative;
  bottom: 90px;
  left: 35px;

  /* box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25); */
}
.movie-info {
  width: 1020px;
  height: 449px;
  position: relative;
  bottom: 150px;
}
.content-header {
  gap: 20px;
  width: 712px;
  height: 152px;
}
.title {
  color: #404040;
  font-family: Poppins;
  font-size: 23px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: inline;
}
.action {
  color: #b91c1c;
  font-family: Poppins;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  width: 84px;
  height: 30px;
  flex-shrink: 0;
  border-radius: 15px;
  border: 1px solid #f8e7eb;
  margin-left: 20px;
}
.drama {
  color: #b91c1c;
  font-family: Poppins;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  width: 84px;
  height: 30px;
  flex-shrink: 0;
  margin-left: 20px;

  border-radius: 15px;
  border: 1px solid #f8e7eb;
}
.star {
  width: 30px;
  height: 30px;
  flex-shrink: 0;
  position: relative;
  left: 230px;
}

.review {
  color: #e8e8e8;
  font-family: Poppins;
  font-size: 25px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  width: 100px;
  margin: 0;
  float: right;
}
.storyline {
  width: 412px;
  height: 182px;
}
.something-button {
  width: 350px;
  height: 55px;
  flex-shrink: 0;
  border-radius: 10px;
  background: #be123c;
  margin-bottom: 20px;
  color: #fff;
  text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.more-button {
  width: 350px;
  height: 55px;
  flex-shrink: 0;
  border-radius: 10px;
  border: 1px solid #be123c;
  background: rgba(190, 18, 60, 0.1);
  color: #333;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.review {
  margin-bottom: 20px;
}
.btns-icon {
  width: 23px;
  height: 23px;
  flex-shrink: 0;

  padding-right: 20px;
}
.writer {
  width: 632px;
  height: 152px;
}
.actors {
  color: #333;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.red-content {
  color: #be123c;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.last-div {
  width: 632px;
  height: 55px;
  flex-shrink: 0;
  border-radius: 10px;
  border: 1px solid #c7c7c7;
  background: rgba(255, 255, 255, 0.8);
  display: flex;
  gap: 30px;
}
.last-btn {
  color: #fff;
  font-family: Poppins;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  width: 253px;
  height: 55px;
  flex-shrink: 0;
  border-radius: 10px;
  background: #be123c;
}
.award {
  color: #333;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.dropdown {
  width: 30px;
  height: 30px;
  flex-shrink: 0;
  background: lightgray 50% / contain no-repeat;
  margin-left: 70px;
  position: relative;
  top: 10px;
}
.last {
  width: 160px;
  height: 70px;
  flex-shrink: 0;
  border-radius: 10px;
  background: lightgray 50% / cover no-repeat;
  position: relative;
  left: 650px;
  bottom: 150px;
}
.last-image {
  width: 360px;
  height: 229px;
  flex-shrink: 0;
  border-radius: 10px;
  /* position: relative;
  left: 100px; */
}
.best-movies-content {
  color: #e8e8e8;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  width: 360px;
  height: 42px;
  flex-shrink: 0;
  border-radius: 10px;
  background: rgba(18, 18, 18, 0.5);
  backdrop-filter: blur(2px);
  position: relative;
  bottom: 60px;
}
.trailer-video {
  border-radius: 10px;
  overflow: hidden;
}
